import React, { useState, useEffect, useRef } from 'react';
import $ from 'jquery'; 

// Path2D for a Heart SVG
const heartSVG = "M0 200 v-200 h200 a100,100 90 0,1 0,200 a100,100 90 0,1 -200,0 z"
const SVG_PATH = new Path2D(heartSVG);

// Scaling Constants for Canvas
const SCALE = 0.05;
const OFFSET = 80;
export const canvasWidth = window.innerWidth;
export const canvasHeight = window.innerHeight;

export function draw(ctx, location){
    console.log(location)
    if(location.type){
        ctx.fillStyle = location.type === 'contract' ? 'red': 'blue';
        ctx.shadowColor = 'blue';
        ctx.shadowBlur = 15;
        ctx.save();
        ctx.scale(SCALE, SCALE);
        var transformedClickX = ctx.canvas.clientWidth - location.x;
        var transformedClickY = location.y - ctx.canvas.offsetTop;
        console.log(ctx.getBoundingClientRect())
        console.log(transformedClickX)
        console.log(transformedClickY)
        ctx.translate(location.x / SCALE - ctx.canvas.offsetLeft, location.y / SCALE - ctx.canvas.offsetTop);
        ctx.rotate(225 * Math.PI / 180);
        ctx.fill(SVG_PATH);
        // .restore(): Canvas 2D API restores the most recently saved canvas state
        ctx.restore();  
    }
};

export const drawMenu = (menu, position) =>{
    if(position){
        menu.style.left = `${position.x}px`;
        menu.style.top = `${position.y}px`;
    }
}

export function useCanvas(){
    const canvasRef = useRef(null);
    const menuRef = useRef(null);

    const [coordinates, setCoordinates] = useState([]);
    const [start, setStart] = useState(false);

    useEffect(()=>{
        const canvasObj = canvasRef.current;
        const ctx = canvasObj.getContext('2d');
        const menu = menuRef.current;
        const _corrdinate = coordinates.length-1;
        // Draw Context Menu 
        if(!start && _corrdinate.type === undefined)    
            drawMenu(menu, coordinates[coordinates.length-1]);
        // clear the canvas area before rendering the coordinates held in state
        ctx.clearRect( 0,0, canvasWidth, canvasHeight );
        // draw all coordinates held in state
        coordinates.forEach((coordinate)=>{
            draw(ctx, coordinate)
        });


    });


    return [ menuRef, coordinates, setCoordinates, canvasRef, canvasWidth, canvasHeight, setStart ];
}
